/**
 * Custom application mixins available through out the app
 */
.gallery {
  display: inline-block;
  vertical-align: top;
  text-align: center;
  background: #001a4c;
  font-weight: bold;
  position: relative;
  width: 100%;
  border: 1px solid grey;
  border-radius: 2px; }

.gallery,
a:hover {
  color: inherit;
  text-decoration: none; }

.coverTitle {
  /* background-color: #FFF; */
  max-height: 28px;
  line-height: 13px;
  padding: 3px;
  overflow: hidden;
  width: 100%;
  height: 4em;
  font-size: 0.85em; }

.extracted > a > .coverTitle {
  color: #00d0ff; }

.caption {
  /* background-color: #FFF; */
  max-height: 35px;
  line-height: 15px;
  padding: 3px;
  overflow: hidden;
  width: 100%;
  z-index: 10;
  font-size: 0.85em;
  color: #a8a8a4; }

.imageContainer {
  display: block;
  max-width: 100%;
  height: auto; }

.gallery .overlay {
  /* Chrome10-25,Safari5.1-6 */
  background: rgba(0, 0, 0, 0.75);
  position: absolute;
  bottom: 0;
  top: 0;
  left: 0;
  right: 0;
  margin-top: 28px;
  margin-bottom: 21px;
  height: auto;
  width: 100%;
  pointer-events: none;
  opacity: 0;
  -webkit-transition: opacity 0.125s ease;
  -o-transition: opacity 0.125s ease;
  transition: opacity 0.125s ease; }

.gallery .overlay .tags {
  width: 100%;
  height: 100%;
  font-size: 14px;
  font-weight: 300;
  line-height: 1.05;
  opacity: 0;
  -webkit-line-clamp: 3;
  overflow: hidden;
  -o-text-overflow: ellipsis;
     text-overflow: ellipsis;
  -webkit-transition: margin 0.175s ease 0.125s, opacity 0.175s ease 0.175s;
  -o-transition: margin 0.175s ease 0.125s, opacity 0.175s ease 0.175s;
  transition: margin 0.175s ease 0.125s, opacity 0.175s ease 0.175s; }

.gallery .overlay .tags .tag {
  width: 100%;
  cursor: pointer; }

.gallery:hover .overlay {
  opacity: 1;
  pointer-events: all; }

.gallery:hover .overlay .tags {
  opacity: 1; }

.gallery:hover .overlay .ListToggle {
  opacity: 1; }

.queryTag {
  color: lightgreen; }

.smallButton {
  margin: 2px; }



