.inlineBlock {
  display: inline-block;
  width: 55px; }

.container {
  position: relative; }

/* Overlays */
.overlay {
  position: absolute;
  max-width: 100%;
  height: 20px;
  z-index: 10;
  color: antiquewhite;
  background-color: rgba(0, 0, 0, 0.5);
  /* dim the background */
  top: 1rem;
  text-align: center; }

.overlayTop {
  top: 0;
  left: 95%;
  text-align: center; }

.overlayHelp {
  position: absolute;
  left: 85%; }

.overlayRight {
  position: absolute;
  right: 1%;
  top: 0;
  z-index: 5; }

.overlayLeft {
  position: absolute;
  left: 1%;
  z-index: 5; }

.imgContainer {
  position: relative; }

.imgContainer:-ms-fullscreen > .overlay {
  display: inherit;
  top: 2%; }

.imgContainer:-webkit-full-screen > .overlay {
  display: inherit;
  top: 2%; }

.imgContainer:-moz-full-screen > .overlay {
  display: inherit;
  top: 2%; }

/* Fullscreen adjustments */
img.active {
  /* width: auto; */
  max-height: 100%;
  margin-left: auto;
  margin-right: auto;
  height: auto;
  max-width: 100%; }

.imgContainer:-ms-fullscreen > .active {
  width: auto;
  max-height: 100%; }

.imgContainer:-webkit-full-screen {
  width: 100%;
  max-height: 100%;
  background-color: black; }

.imgContainer:-webkit-full-screen > .active {
  max-height: 100%; }

.imgContainer:-moz-full-screen > .active {
  width: auto;
  max-height: 100%; }

.modalContainer {
  position: absolute;
  height: 100px;
  width: 95%; }

.tag {
  background-color: rgba(0, 0, 0, 0.5);
  /* dim the background */
  display: table; }

.HotKeys:focus {
  outline: 0; }



