.inlineBlock {
  display: inline-block;
  width: 55px; }

/* Overlays */
.overlay {
  position: absolute;
  max-width: 100%;
  height: 20px;
  z-index: 10;
  color: antiquewhite;
  background-color: rgba(0, 0, 0, 0.5);
  /* dim the background */
  top: 1rem;
  text-align: center; }

.overlayTop {
  top: 0;
  left: 95%;
  text-align: center; }

.overlayRight {
  left: 85%;
  text-align: center; }

.hide {
  display: none; }

.overlayLeft {
  left: 1%;
  text-align: left; }

.imgContainer {
  position: relative; }

.imgContainer:-ms-fullscreen > .overlay {
  display: inherit;
  top: 2%; }

.imgContainer:-webkit-full-screen > .overlay {
  display: inherit;
  top: 2%; }

.imgContainer:-moz-full-screen > .overlay {
  display: inherit;
  top: 2%; }

/* Fullscreen adjustments */
img.active {
  /* width: auto; */
  max-height: 100%;
  margin-left: auto;
  margin-right: auto;
  height: auto;
  max-width: 100%;
  -webkit-transition-property: -webkit-transform;
  transition-property: transform, -webkit-transform;
  -webkit-transition-duration: 0.2s;
  -o-transition-duration: 0.2s;
     transition-duration: 0.2s;
  -webkit-transition-timing-function: ease-out;
  -o-transition-timing-function: ease-out;
     transition-timing-function: ease-out; }

.imgContainer:-ms-fullscreen > .active {
  width: auto;
  max-height: 100%; }

.imgContainer:-webkit-full-screen {
  width: 100%;
  height: 100%;
  background-color: black; }

.imgContainer:-webkit-full-screen > .active {
  width: auto; }

.imgContainer:-moz-full-screen > .active {
  width: auto;
  max-height: 100%; }

.modalContainer {
  position: absolute;
  height: 100px;
  width: 95%; }

.tag {
  background-color: rgba(0, 0, 0, 0.5);
  /* dim the background */
  display: table; }

.HotKeys:focus {
  outline: 0; }



